// import React from 'react'
import React,{useState} from 'react';


export const AddTodo = (props) => {
    const [title, setTitle] = useState("");
    const submit=(e)=>{
        e.preventDefault();
        if(!title)
        {alert('Fill a task');}
      else
     {
      props.addTodo(title);
        setTitle("");
    }
    }
   
    return (
        <>
    <div className="container my-3" >
        
          <form onSubmit={submit}>
  <div class="mb-3">
  
    <div id="emailHelp" class="form-text">Lets get on track</div>
    <input type="text" value={title} onChange={(e)=>{setTitle(e.target.value)}} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Add a todo"/>
  </div>
  
  {/* <div class="mb-3 form-check">
    <input type="checkbox" class="form-check-input" id="exampleCheck1"/>
    <label class="form-check-label" for="exampleCheck1">Check me out</label>
  </div> */}
  <button type="submit" class="btn btn-success btn-m">Add a Todo</button>
</form>  
        </div>
        </>
    )
}
