import React from 'react'

export default function TodoItem({todo,onDelete}) {
    // console.log('hey');
    return (
        <div>
              
            {/* <h4>
                {todo.sno}
            </h4> */}
            <p>
            {todo.title}
            </p>
            <button className="btn btn-danger btn-m my-1" onClick={()=>{onDelete(todo)}}>Delete</button>
            <hr/>
        </div>
    )
}
